var Note = /** @class */ (function () {
    function Note(note, enabled) {
        this.note = note;
        this.enabled = enabled;
    }
    return Note;
}());
var todoList = [new Note("Kom ihåg att köpa mjölk", true), new Note("Glöm inte tandkräm!", true), new Note("Bli klar med tentan innan torsdag nästa vecka!", true)];
window.onload = function () {
    presentList(todoList);
};
function presentList(todoList) {
    var ul = document.getElementById("todoListUl");
    ul.innerHTML = " ";
    var _loop_1 = function (i) {
        if (todoList[i].enabled == true) {
            var newLi = document.createElement("li");
            var newLiButton = document.createElement("button");
            newLiButton.innerHTML = "Radera";
            newLi.appendChild(newLiButton);
            newLi.innerHTML = todoList[i].note + "\t";
            newLi.id = JSON.stringify(i);
            newLiButton.addEventListener("click", function () {
                removeFromList(i);
            });
            newLi.appendChild(newLiButton);
            ul.appendChild(newLi);
        }
    };
    for (var i = 0; i < todoList.length; i++) {
        _loop_1(i);
    }
}
function removeFromList(noteToRemove) {
    todoList[noteToRemove].enabled = false;
    presentList(todoList);
}
function returnToList(noteToEnable) {
    todoList[noteToEnable].enabled = true;
    presentList(todoList);
}
function addNote() {
    try {
        var newNote;
        newNote = document.getElementById("newNoteInput").value;
        ;
        todoList.push(new Note(newNote, true));
        presentList(todoList);
    }
    catch (err) {
        alert("Error!");
    }
}
function switchPlaces(firstNote, secondNote) {
    var buffer;
    buffer = todoList[firstNote];
    todoList[firstNote] = todoList[secondNote];
    todoList[secondNote] = buffer;
    presentList(todoList);
}
